instance.interceptors.response.use(
  // 
  (res) => {
    // NProgress.done()// 
    return res.data
  },
  (err) => {
    if (err && err.response) {
      switch (err.response.status) {
        case 400:
          console.log('')
          break
        case 401:
          console.log('')
          break
        default:
          console.log('')
      }
    }
    return err
  }
)

export default instance
